@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:700&display=swap);
html, body{
  height: 100%;
}


body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

body{
  background: #192a56;
}

.dimension{
  grid-column: 1/3;
  justify-self: center;
}

#dimension{
   width: 200px;
   height: 50px;
}

.title{
  
  font-size: 60px;
}



.nonfilter-number{
  font-size: 25px;
  padding-left: 15px;
}
#display, #convolved-display{
    background: #515151;
}

.image-display{
   height: -webkit-fit-content;
   height: -moz-fit-content;
   height: fit-content;
   justify-self: center;
   align-self: center;
   justify-content: center;
   display: grid;
   grid-template-areas: "original convolved"
                        "original-canvas new-canvas"
                        "image-button download-button"
}

#original {
    grid-area: original
}

#convolved {
    grid-area: convolved
}

#display {
    grid-area: original-canvas
}

#convolved-display{
    grid-area: new-canvas
}

#image-button{
    grid-area: image-button
}

#download-button{
    grid-area: download-button
}

input[type="file"]{
   max-height: 100px;
   display: none;
}

.image-button{
    height: 50px;
    text-align: center;
    font-size: 16px;
    background: slategray;
    cursor: pointer;
    margin: 10px;
}

a.image-button{
    line-height: 3;
    text-decoration: none;
    color: black
}

canvas{
    margin: 15px;
}

@media (max-width: 1350px){
    canvas{
        height: 300px;
        width: 300px;
    }

    .image-display {
        grid-template-areas: "original"
                             "original-canvas"
                             "convolved"
                             "new-canvas"
                             "image-button"
                             "download-button"
    
    }

    .image-display h2{
        margin-top: 5px;
        margin-bottom: 5px;
    }

    canvas{
        margin: 0px;
    }

    .title{
        margin-top: 15px;
        margin-bottom: 15px;
    }
}


h1, h2, h3, label{
    color: white;
    font-family: 'Roboto Slab';
}

.convolution-container{
    display: grid;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 500px;
    grid-area: filter;
    justify-self: center;
}

#filter-title{
    grid-area: title
}

.convolution{
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-areas: "title"
                         "filter"
                         "scale"
                         "size"
                         "presets";
}

.setting{
    margin: 15px;
}

.filter-number{
    width: 50px;
    height: 50px;
    align-self: center;
    margin-left: auto !important;
    margin-right: auto;
    text-align: center;
}

.size{
    grid-area: size
}

.filter-scale{
    margin-top: 50px;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
    grid-area: scale
}

.filter-scale input{
    width: 200px;
    height: 50px; 
}

input[type="number"], select{
    margin-left: 20px   
}
